/**
 * @usedBy PoTableRowTemplateDirective
 *
 * @description
 * Define a posio da arrow que expande o * row template* na tabela, ser exibida na esquerda ou direita.
 */
export enum PoTableRowTemplateArrowDirection {
  /** Posiciona a *arrow* na esquerda. (Padro) */
  Left = 'LEFT',

  /** Posiciona a *arrow* na direita */
  Right = 'RIGHT'
}
